export default [
  {
    name: 'join',
    params: [
      { key: 'type', value: 'any' },
      { key: 'required', value: 'true' }
    ],
    details: `
        it is a valid html selector as an id, a class or any other 
        properties allowing to select the element attached to the dropdown. 
        The dropdown position will be calculated based on the position 
        of the join element.
      `
  },
  {
    name: 'align',
    params: [
      { key: 'type', value: 'string' },
      { key: 'required', value: 'false' },
      { key: 'value', value: 'left | right | top | bottom' }
    ],
    details: `
       This property defines the dropdown alignment. The latter can be placed on the left, right, top or bottom. If it is not, the alignment will be calculated automatically with respect to the 
       <code  class="font-weight-bold">direction</code> property.
      `
  },
  {
    name: 'direction',
    params: [
      { key: 'type', value: 'string' },
      { key: 'required', value: 'false' },
      { key: 'value', value: 'x | y' }
    ],
    details: `
        This property defines the alignment direction of the dropdown. If its value is <code  class="font-weight-bold">x</code>, the dropdown will align either to the left or to the right of the join element. If its value is <code  class="font-weight-bold">y</code>, the dropdown will be aligned either at the top or at the bottom of the join element. The default value is <code  class="font-weight-bold">y</code>. If the <code  class="font-weight-bold">align</code> property is defined, the <code  class="font-weight-bold">direction</code> property is not taken into account to calculate the position of the dropdown.
      `
  },
  {
    name: 'hide-arrow',
    params: [
      { key: 'type', value: 'boolean' },
      { key: 'default', value: 'false' }
    ],
    details: `
        This property allows you to display or hide the arrow.
      `
  },
  {
    name: 'justify',
    params: [
      { key: 'type', value: 'boolean' },
      { key: 'default', value: 'false' }
    ],
    details: `
        If true, the dropdown will be placed in the middle of the join element. This property is only taken into account if the display screen allows centered alignment
      `
  },
  {
    name: 'show-on',
    params: [
      { key: 'type', value: '[string, array]' },
      { key: 'required', value: 'false' },
      { key: 'default', value: 'click' }
    ],
    details: `
        this property defines the events of the join element which will trigger the opening of the dropdown. It can be a 
         <code  class="font-weight-bold">string</code> if it is a single event or an array of  <code  class="font-weight-bold">string</code> if it is several. The default value is  <code  class="font-weight-bold">click</code>
      `
  },
  {
    name: 'hide-on',
    params: [
      { key: 'type', value: '[string, array]' },
      { key: 'required', value: 'false' }
    ],
    details: `
        this property defines the events of the join element which will trigger the closure of the dropdown. It can be a 
         <code  class="font-weight-bold">string</code> if it is a single event or an array of  <code  class="font-weight-bold">string</code> if it is several.
      `
  },
  {
    name: 'z-index',
    params: [
      { key: 'type', value: 'number' },
      { key: 'required', value: 'true' },
      { key: 'default', value: '50000' }
    ],
    details: `Choose a large value so that the dropdown is not hidden by other dropdowns.`
  },
  {
    name: 'position-on-resize',
    params: [
      { key: 'type', value: 'boolean' },
      { key: 'default', value: 'true' }
    ],
    details: `If true, the position of the dropdown will be calculated each time the size of the window is modified.`
  },
  {
    name: 'position-on-scroll',
    params: [
      { key: 'type', value: 'boolean' },
      { key: 'default', value: 'false' }
    ],
    details: `If true, the position of the dropdown will be calculated with each scroll of the window.`
  }
];
